.pb_phone_number_input input::placeholder {
  color: rgba(247, 251, 255, 0.5); }

.pb_phone_number_input .dropdown_open .text_input {
  border-color: #0056CF !important; }

.pb_phone_number_input .dropdown_open .iti__selected-flag:focus-visible {
  outline-style: none; }

.pb_phone_number_input .iti__country {
  padding: 5px 10px 5px 16px;
  transition: .3s; }

.pb_phone_number_input .iti__selected-flag {
  padding: 0 4px 0 16px; }

.pb_phone_number_input .iti__selected-flag[aria-expanded="true"] {
  color: #0056CF; }

.pb_phone_number_input .iti__selected-flag:focus-visible {
  outline-style: solid;
  border-radius: 4px 0px 0px 4px;
  outline-color: #0056CF; }

.pb_phone_number_input .iti__country.iti__highlight {
  background-color: #e0eaf5; }

.pb_phone_number_input .iti__country.iti__highlight .iti__country-name, .pb_phone_number_input .iti__country.iti__highlight .iti__dial-code {
  color: #0056CF; }

.pb_phone_number_input .iti--allow-dropdown .iti__flag-container:hover .iti__selected-flag {
  background-color: rgba(247, 251, 255, 0.5); }

.pb_phone_number_input .iti__flag-container:hover + .text_input {
  background-color: rgba(247, 251, 255, 0.5); }

.pb_phone_number_input .iti__flag {
  background-image: url("https://cdnjs.cloudflare.com/ajax/libs/intl-tel-input/17.0.19/img/flags.png");
  border-radius: 1px; }

.pb_phone_number_input .iti--separate-dial-code {
  width: 100%; }

.pb_phone_number_input .iti--separate-dial-code .iti__selected-flag {
  background-color: rgba(255, 255, 255, 0.1); }

.pb_phone_number_input .iti__arrow {
  border-left: unset;
  border-right: unset;
  border-top: unset;
  width: unset;
  height: unset;
  margin-bottom: 4px; }

.pb_phone_number_input .iti__arrow--up {
  border-bottom: unset; }

.pb_phone_number_input .iti__arrow::before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  content: '';
  display: inline-block;
  height: 5px;
  left: 1px;
  position: relative;
  vertical-align: top;
  width: 5px;
  top: 10px;
  transform: rotate(135deg);
  color: #C1CDD6; }

.pb_phone_number_input .iti__arrow.iti__arrow--up::before {
  transform: rotate(-45deg);
  top: 12px;
  color: #0056CF; }

.pb_phone_number_input .iti__active::after {
  float: right;
  content: "";
  margin-top: 5px;
  transform: rotate(45deg);
  height: 12px;
  width: 6px;
  border-bottom: 2px solid;
  border-right: 2px solid;
  border-radius: 1px; }

.pb_phone_number_input .iti__country-list {
  min-width: 340px;
  border-radius: 6px;
  border: 1px solid #E4E8F0;
  box-shadow: 0 4px 10px 0 rgba(60, 106, 172, 0.16);
  margin-top: 1px; }

.pb_phone_number_input .iti__divider {
  border-bottom: 1px solid #E4E8F0; }

.pb_phone_number_input.dark .iti--allow-dropdown .iti__flag-container {
  background-color: rgba(255, 255, 255, 0); }

.pb_phone_number_input.dark .iti--allow-dropdown .iti__flag-container:hover {
  background-color: rgba(255, 255, 255, 0); }

.pb_phone_number_input.dark .iti--allow-dropdown .iti__flag-container:hover + .text_input {
  background-color: rgba(255, 255, 255, 0.15); }

.pb_phone_number_input.dark .iti--allow-dropdown .iti__flag-container:hover .iti__selected-flag {
  background-color: rgba(255, 255, 255, 0); }

.pb_phone_number_input.dark .iti__selected-flag {
  background-color: rgba(255, 255, 255, 0);
  color: #fff; }

.pb_phone_number_input.dark .iti__country-list {
  background-color: #0a0527;
  border: 1px solid #3b3752; }

.pb_phone_number_input.dark .iti__country-list .iti__highlight {
  background-color: rgba(255, 255, 255, 0.2); }

.pb_phone_number_input.dark .iti__country-list .iti__highlight .iti__country-name {
  color: #fff; }

.pb_phone_number_input.dark .iti__country-list .iti__highlight .iti__dial-code {
  color: #C1CDD6; }

.pb_phone_number_input.dark .iti__divider {
  border-bottom: 1px solid #3b3752; }

.pb_phone_number_input.dark .iti__arrow.iti__arrow--up::before {
  color: #C1CDD6; }

.pb_phone_number_input.dark .dropdown_open .text_input {
  background-color: rgba(255, 255, 255, 0.025) !important; }

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .pb_phone_number_input .iti__flag {
