.pb_multi_level_select {
  font-family: "Proxima Nova","Helvetica Neue",Helvetica,Arial,sans_serif; }

.pb_multi_level_select .wrapper {
  position: relative; }

.pb_multi_level_select .wrapper .toggle_icon {
  visibility: hidden; }

.pb_multi_level_select .wrapper .toggle_icon .pb_button_content {
  visibility: hidden !important; }

.pb_multi_level_select .wrapper .input_wrapper {
  background-color: #fff;
  cursor: pointer;
  padding: 12px 16px;
  border: 1px solid #e4e8f0;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.pb_multi_level_select .wrapper .input_wrapper .input_inner_container {
  max-width: 90%; }

.pb_multi_level_select .wrapper .input_wrapper input {
  border: none;
  font-family: "Proxima Nova","Helvetica Neue",Helvetica,Arial,sans_serif;
  padding: 8px; }

.pb_multi_level_select .wrapper .input_wrapper input:focus {
  outline: none; }

.pb_multi_level_select .dropdown_menu {
  position: absolute;
  z-index: 100;
  width: 100%;
  background-color: #fff;
  padding: 16px 16px 16px 0;
  border-radius: 6px;
  border: 1px solid #e4e8f0;
  max-height: 600px;
  overflow: auto; }

.pb_multi_level_select .dropdown_menu ul {
  padding: 0px !important; }

.pb_multi_level_select .dropdown_menu .dropdown_item {
  list-style: none;
  padding-left: 20px; }

.pb_multi_level_select .dropdown_menu .dropdown_item .dropdown_item_checkbox_row {
  display: flex;
  align-items: center; }

.pb_multi_level_select .dropdown_menu .dropdown_item .dropdown_item_checkbox_row .pb_checkbox_kit {
  align-items: center; }

.pb_multi_level_select .dropdown_menu .dropdown_item .dropdown_item_checkbox_row .pb_button_kit_link_inline_enabled {
  color: #242B42; }

.pb_multi_level_select .dropdown_menu .dropdown_item .dropdown_item_checkbox_row .pb_button_kit_link_inline_enabled:hover {
  color: #0056CF; }

.pb_multi_level_select .close {
  display: none; }

.pb_multi_level_select .open {
  display: block; }

