.pb--page--topNav {
  flex: 0 0;
  padding: 16px 24px 8px 24px;
  background: #FFFFFF;
  position: relative; }
  .pb--page--topNav h1 {
    display: flex;
    align-items: center; }
    .pb--page--topNav h1 img {
      display: flex; }
  .pb--page--topNav--separator {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%; }
  .pb--page--topNav.dark {
    background: #231e3d; }
  .pb--page--topNav .desktop-kit-search {
