      margin-bottom: 0 !important; }
  .pb--page--topNav .mobile-kit-search {
    display: none; }
  .pb--page--topNav .pb-version-tag {
    text-align: right;
    position: relative;
    bottom: 20px; }
  .pb--page--topNav .separator-explicit-height {
    height: 45px;
    margin-left: 8px;
    margin-right: 8px; }
  @media screen and (max-width: 992px) {
